.footer {
  background-color: $color-bg-footer;

  & ul {
    padding: 0;
    list-style-type: none;
  }

  & a {
    text-decoration: none;
    display: block;
    color: $color-default-white;

    @include hover-focus {
      opacity: 0.7;
    }
  }
}

.footer__container {
  display: flex;
  align-items: center;
  min-height: 108px;

  @include vp-1199 {
    min-height: 114px;
  }

  @include vp-767 {
    min-height: 260px;
    flex-wrap: wrap;
  }
}

.footer__logo-wrapper {
  margin-right: auto;

  @include vp-767 {
    display: none;
  }

  & svg {
    margin-top: 20px;
  }
}
